.catalog {
  color: var(--primeTextColor);

  &__title {
    margin: 24px 0 8px;
    font-size: 32px;
    line-height: 40px;
    font-weight: 800;
    letter-spacing: -0.32px;

    @media (min-width: 640px) {
      font-size: 48px;
      line-height: 56px;
    }
  }

  &__items-count {
    margin-top: 8px;
    margin-bottom: 32px;
    color: var(--secondaryTextColor);
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
  }

  &__dropdown-container {
    width: 100%;
    margin-bottom: 24px;
    display: flex;
    justify-content: space-between;
    gap: 16px;

    @media(min-width: 640px) {
      width: 60%;
    }

    @media(min-width: 768px) {
      width: 45%;
    }

    @media(min-width: 1200px) {
      width: 30%;
    }
  }

  &__dropdown {
    flex: auto;

    @media(min-width: 640px) {
      &:first-child {
        width: 58%;
      }
    }
  }

  &__pagination {
    margin-top: 24px;

    @media(min-width: 640px) {
      margin-top: 40px;
    }
  }
}
